@import '../../Lobby.scss';

.ant-modal {
    width: 95% !important;
    border: 2px solid black;
    outline: $text-color solid 2px;
    padding: 0;
}

.ant-modal-content {
    background-color: $background-color;
}

.ant-modal-header {
    background: $background-color;
}

.ant-modal-title {
    color: $text-color;
}

.ant-modal-footer {
    border-top: 0px;
}
