@import '../../Lobby.scss';

.margin {
    margin-left: 8px;
}

.header {
    @extend .margin;

    display: flex;
    align-items: center;
    line-height: 0px;
    margin-bottom: 4px;
}

.container {
    display: flex;
    justify-content: space-between;
    border-style: solid;
    border-radius: 10px;
    width: 95%;
    padding: 6px;
    margin-bottom: 4px;
}

.infoContainer {
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.numberContainer {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: space-evenly;
}

.numberContainer > span {
    font-size: 18px !important;
    line-height: 18px;
}

.name {
    margin-left: 6px;
}

.blurb {
    @extend .margin;
    font-size: small !important;
    color: $minor-text-color !important;
    line-height: 10px;
}
