@import '../../Lobby.scss';

.header {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

.title {
    font-size: 28px !important;
    line-height: 24px;
    padding-bottom: -16px !important;
    text-align: left;
}

.description {
    text-align: left;
    white-space: normal;
    font-size: 16px !important;
    line-height: 14px;
    color: $minor-text-color !important;
}

.description * {
    @extend .description;
}

.subdescription {
    @extend .description;
    font-size: 10px;
    line-height: 12px;
    color: $minor-text-color !important;
}

.secondaryResource {
    font-size: 16px !important;
}

.corner {
    font-size: 18px !important;
    letter-spacing: -0.5px;
    display: flex;
    align-items: center;
}

.corner * {
    @extend .corner;
}
