@import '~antd/dist/antd.css';

@font-face {
    font-family: retro;
    src: url(../resources/Itim-Regular.ttf);
}

$background-color: #1b1b1b;
$important-color: #ffd700;
$text-color: #f9f9f9;
$minor-text-color: Gainsboro;

html * {
    padding: 0;
    margin: 0;
    font-family: 'retro', sans-serif;
    color: $text-color;
}

html,
body {
    max-width: 100%;
    overflow-x: hidden;
}

body {
    padding: 0;
    margin: 0;
    background-color: $background-color;
}

#root {
    width: 100%;
    height: 100%;
}

.App {
    text-align: center;
}

.element {
    text-align: center;
}

button:disabled {
    background-color: #332020 !important;
    border-color: #854949 !important;
}

span,
p {
    font-family: 'retro', sans-serif;
    font-size: 24px !important;
}

.ant-tabs-tab {
    font-size: 32px;
    min-width: 10%;
    text-align: center;
}

.ant-tabs.ant-tabs-card .ant-tabs-card-bar .ant-tabs-tab {
    background: $background-color;
    color: $text-color;

    border-radius: 10px 10px 0 0;
}

.ant-tabs-tab-active {
    color: $important-color !important;
    border-bottom: 0px !important;
}

p {
    color: $text-color !important;
    font-size: 12px;
}

span {
    color: $text-color !important;
}

.center {
    text-align: center;
    margin: 0;
    margin-top: -20px;
    margin-bottom: 10px;
    padding: 0;
}

h1.ant-typography {
    color: $text-color;
    font-weight: normal;
    margin: 0 0 0 0;
}

.ant-progress {
    margin-top: 12px;
    margin-bottom: 12px;
}

.ant-progress-bg {
    height: 16px !important;
}

.ant-progress-text {
    width: 100%;
    position: absolute;
    display: block;
    text-align: center;
    margin-top: -1.5%;
    z-index: 1;
    font-size: 32px;
    -webkit-text-stroke-width: 0.5px;
    -webkit-text-stroke-color: $background-color;
    text-shadow: 1px 1px $background-color;
}

.ant-input-affix-wrapper {
    background-color: $background-color;
    border-width: 0 0 2px;
    outline: 0;
}

.ant-input {
    background-color: $background-color;
    color: $text-color;
}

.center-container {
    height: 80%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.largeText {
    font-size: 80px;
}

.ant-typography + h1.ant-typography,
.ant-typography + h2.ant-typography,
.ant-typography + h3.ant-typography,
.ant-typography + h4.ant-typography {
    margin-top: 0;
}

.ant-modal-title {
    padding-right: 8px;
}

.element {
    width: 95%;
}

.center-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.list-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.positive {
    color: mediumspringgreen !important;
}

.negative {
    color: palevioletred !important;
}

.neutral {
    color: $minor-text-color !important;
}
