@import '../../Lobby.scss';

.textContainer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: right;
    width: 100%;
}

.button {
    height: 60px;
}

.pass {
    border-color: grey !important;
}

.roll {
    border-color: lime !important;
}

.icon {
    display: flex;
}

.text {
    font-size: 34px !important;

    text-align: right;
    display: inline-block;
    vertical-align: middle;
    line-height: normal;

    line-height: 24px;
    white-space: normal;
}

.subtext {
    font-size: 20px !important;

    text-align: left;
    display: inline-block;
    vertical-align: middle;
    line-height: normal;

    line-height: 20px;
    white-space: normal;
}

.container {
    display: flex;
    flex-direction: row;
}

.checkboxContainer {
    display: flex;
    align-items: center;
    justify-content: center;
}
